import React, {Component} from 'react';
import './index.css';
import dummy from './assets/surya.jpg';
import BgImage from '../../assets/flowers.png';

const PersonWrapperGrid = (props) => {
    return(
        <div className="mepandes__person-wrapper">
            <div className="mepandes__person-img">
                <img src={dummy} alt=""/>
            </div>
            <p style={{
                textAlign: "center",
                fontFamily: "'Roboto', sans-serif",
                fontSize: "24px",
                fontWeight: "bold",
                margin: "0 10px"
            }}>MARK ZUCKERBERG</p>
            <p style={{
                textAlign: "center",
                fontFamily: "'Roboto', sans-serif",
                fontSize: "14px",
                margin: "0 10px"
            }}>Mepandes</p>
            <div style={{
                position:"absolute",
                height: "103px",
                width: "290px",
                padding: "20px 40px",
                backgroundColor: "#B22222",
                top: "190px"
            }}>
                <p style={{
                    textAlign: "center",
                    fontFamily: "'Roboto', sans-serif",
                    fontSize: "24px",
                    fontWeight: "bold",
                    margin: "0 10px",
                    color: "#FFF"
                }}>MARK ZUCKERBERG</p>
                <p style={{
                    textAlign: "center",
                    fontFamily: "'Roboto', sans-serif",
                    fontSize: "14px",
                    margin: "0 10px",
                    color: "#FFF"
                }}>Mepandes</p>
            </div>
        </div>              
    )
}

const PersonWrapperBubble= () => {
    return(
        <div className="mepandes__person-wrapper bubble">
            <div className="mepandes__person-image-wrapper" style={{
                textAlign: "center"
            }}>
                <div className="mepandes__person-img" style={{
                    backgroundImage: `url(${dummy})`,
                    width: "100px",
                    height: "100px",
                    borderRadius: "500px",
                    backgroundPosition: "center",
                    backgroundSize: "cover",
                    display: "inline-block",
                    verticalAlign: "middle"
                }}></div>
            </div>
            <div style={{         
                textAlign: "center"       
            }}>
                <p style={{
                    fontSize: "20px",
                    color: "#FFF",
                    fontFamily: "'Roboto', sans-serif"
                }}>Surya Darma</p>                
            </div>
        </div>
    )
}

export default class MepandesBlock extends Component{
    render(){
        return(
            <section className="mepandes__wrapper">
                <h2 className="mepandes__section-title">Sane Mepandes</h2>                
                <hr style={{
                    width: "135px",
                    margin: "5px auto 15px",                    
                    border: "1px solid #b43e3e"
                }}/>
                <div style={{
                    backgroundImage: `url(${BgImage})`,
                    padding: "25px 0",
                    backgroundColor: "#B22222"
                }}>
                    <div style={{
                        maxWidth: "1100px",
                        margin: "auto"
                    }}>
                        <div style={{
                            textAlign: "center"
                        }}>
                            <PersonWrapperBubble />    
                            <PersonWrapperBubble />    
                            <PersonWrapperBubble />    
                        </div>
                        <div style={{
                            textAlign: "center"
                        }}>
                            <PersonWrapperBubble />    
                            <PersonWrapperBubble /> 
                        </div>   
                    </div>
                </div>                
            </section>
        )
    }
}


