import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import StickyScrollHeader from '../../components/StickyScrollHeader';
import GreetingBlock from '../../components/GreetingBlock';
import SlideSlider from'../../components/SlideSlider';
import MepandesBlock from '../../components/MepandesBlock';
import SnippetGallery from '../../components/SnippetGallery';
import GoogleMaps from '../../components/GoogleMaps';

export default class Home extends Component{
    render(){
        return(
            <div className="home">
                <SlideSlider />
                <StickyScrollHeader/>
                <GreetingBlock />
                <MepandesBlock/>
                <SnippetGallery/>                
                <div style={{
                    margin: "20px auto",
                    textAlign: "center"
                }}>
                    <Link to={'/gallery'} className="readmore-btn">
                        <span className="text">See more</span>
                        <span className="oi arrow" data-glyph="arrow-thick-right"></span>
                        {/* <span className="arrow">&#x2192;</span> */}
                    </Link>
                </div>          
                <GoogleMaps />                        
            </div>
        )
    }
}


