import React, {Component} from 'react';
import './index.css';
import image1 from './assets/WP2_9669.JPG';
import image2 from './assets/WP1_7972.JPG';
import image3 from './assets/WP1_7991.JPG';
import image4 from './assets/WP1_8123.JPG';
import image5 from './assets/WP2_9576.JPG';

export const LandscapeWrapper = (props) => {
    return(
        <div className="snippet-gallery__image-wrapper one" onClick={props.OpenModalHandler}>
            <div className="snippet-gallery__image" style={{
                backgroundImage: `url(${props.image})`
            }}>                    
            </div>                    
            <div className="snippet-galley__image-caption-wrapper">
                <p className="image-caption__place">SANGEH</p>
                <p className="image-caption__date">May 27<sup>th</sup>, 2018</p>
            </div>                    
        </div>
    )
}

export const PotraitWrapper = (props) => {
    return(
        <div className="snippet-gallery__image-wrapper two" onClick={props.OpenModalHandler}>
            <div className="snippet-gallery__image" style={{
                backgroundImage: `url(${props.image})`
            }}>                    
            </div>                    
            <div className="snippet-galley__image-caption-wrapper">
                <p className="image-caption__place">SANGEH</p>
                <p className="image-caption__date">May 27<sup>th</sup>, 2018</p>
            </div>                    
        </div>
    )
}

const ModalShowImage = (props) => {
    return(
        <div className={`snippet-gallery__modal-wrapper ${props.isModalOpen ? "open" : "close"} ${props.modalOrientation}`} id="image-modal">            
            <div className="snippet-gallery__modal-content">
                <p className="close" onClick={props.CloseModalHandler}>&times;</p>                        
                <img src={props.image} />
                <p className="snippet-gallery__image-name">{props.name}</p>        
            </div>
        </div>
    )
}

export default class SnippetGallery extends Component{
    constructor(props){
        super(props);
        this.state = {
            isModalOpen: false,
            imageInModal: image1,
            modalOrientation: ""
        }
        this.CloseModalHandler = this.CloseModalHandler.bind(this);
        this.OpenModalHandler = this.OpenModalHandler.bind(this);
    }  

    CloseModalHandler = () => {
        const body = document.getElementsByTagName('body')[0];
        body.style.overflow = 'auto';
        this.setState({
            isModalOpen: false
        })
    }

    OpenModalHandler(options){        
        const body = document.getElementsByTagName('body')[0];
        body.style.overflow = 'hidden';        
        this.setState({
            isModalOpen: true,
            imageInModal: options.image,
            modalOrientation: options.orientation
        })
    }

    componentDidMount(){
        const modal = document.getElementById("image-modal");
        window.onclick = (event) => {
            console.log(modal);
            console.log(event.target.id);
            if (event.target.id === "image-modal") {
                this.CloseModalHandler();
            }         
        }
    }

    render(){
        return(
            <section className="snippet-gallery__wrapper">
                <h2 className="snipet-gallery__section-title">Our Snapshoot</h2>
                <hr style={{
                    width: "135px",
                    margin: "5px auto 15px",                    
                    border: "1px solid #b43e3e"
                }}/>
                <div className="snipet-gallery__row">
                    <LandscapeWrapper 
                        image={image2}
                        OpenModalHandler={() => this.OpenModalHandler({
                            image: image2,
                            orientation: "landscape"
                        })}
                    />
                    <LandscapeWrapper 
                        image={image4}
                        OpenModalHandler={() => this.OpenModalHandler({
                            image: image4,
                            orientation: "landscape"
                        })}
                    />
                </div>
                <div className="snipet-gallery__row">
                    <PotraitWrapper 
                        image={image5}
                        OpenModalHandler={() => this.OpenModalHandler({
                            image: image5,
                            orientation: "potrait"
                        })}
                    />
                </div>
                <div className="snipet-gallery__row">
                    <LandscapeWrapper 
                        image={image1}
                        OpenModalHandler={() => this.OpenModalHandler({
                            image: image1,
                            orientation: "landscape"
                        })}
                    />
                    <LandscapeWrapper 
                        image={image3}
                        OpenModalHandler={() => this.OpenModalHandler({
                            image: image3,
                            orientation: "landscape"
                        })}
                    />
                </div>
                {
                    this.state.isModalOpen?
                    <ModalShowImage 
                        image={this.state.imageInModal} 
                        modalOrientation={this.state.modalOrientation}
                        name="Sangeh - May 27th, 2018" 
                        isModalOpen={this.state.isModalOpen} 
                        CloseModalHandler={this.CloseModalHandler}
                    /> : 
                    ""
                }                                        
            </section>
        )        
    }
}


