import React, {Component} from 'react';
import Key from './key.json';

export default class GoogleMaps extends Component{    
    render(){
        return(
            <section>
            <iframe
                width="100%"
                height="250"
                frameBorder="0"
                src={`https://www.google.com/maps/embed/v1/place?key=${Key.api_key}
                    &q=jalan+pintas+siligita+no+27`}
                allowFullScreen
            >
            </iframe>
            </section>
        )
    }
}




